/**
 * @file
 * Tablesort indicator styles.
 */
.tablesort {
  height: 14px;
  margin: 1px 0 0 5px;
  vertical-align: top;
  width: 14px;
}

.tablesort--asc {
  background-image: url("/core/misc/icons/787878/twistie-up.svg");
}

.tablesort--desc {
  background-image: url("/core/misc/icons/787878/twistie-up.svg");
}

/**
 * Fix ckeditor image align via "add media"
 */
div.align-center img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: auto;
}

/**
 * Fix ckeditor videos aspect ratio
 */
.field--name-field-media-oembed-video {
  overflow: hidden;
  padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
  position: relative;
  width: 100%;
}

.media-oembed-content {
  border: 0;
  bottom: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

