  flex: 1;
}
.faq-question span::before {
  background: url("../images/arrow-down-blue.svg") no-repeat center center;
  border: 0;
  content: "";
  height: 14px;
  transition: transform 200ms;
  vertical-align: middle;
  width: 30px;
}
.faq-question.faq-qa-visible span::before {
  transform: rotate(180deg);
}

